.attention{
   width: 30%;
   min-width: 310px;
   min-height: 190px;
   max-height: 400px;

   display: none;
   flex-direction: column;
   justify-content: space-between;

   background-color: rgb(255,255,255, 0.8);
   border-radius: 15px;
   box-shadow: 0 0 20px rgb(255,255,255, 0.8);

   text-align: center;

   position: absolute;
   top: 35%;
   left: 35%;
   z-index: 52;

   overflow: hidden;
}

.attention span{
   width: 100%;
   height: 80%;

   font-size: 0.875em;
}

.attention .title{
   width: 100%;
   height: 10px;

   margin-top: 3%;

   font-size: 1.25em;
   font-weight: 600;
   color: cadetblue;
   letter-spacing: 1px;
}

.attention .txt{
   height: auto;
   padding: 0 5% 0 5%;
}

.attention button{
   width: 100%;
   min-height: 50px;

   position: relative;

   font-size: 1.125em;
   font-weight: 800;
   color: cadetblue;
   letter-spacing: 1px;
}

.attention button::before{
   content: '';

   width: 100%;
   height: 2px;

   display: block;
   position: absolute;
   top: 0;
   left: 0;

   background-color: cadetblue;
}

.fadeIn {
   animation: fadeIn 1s ease-in-out;
}

.fadeOut{
   animation: fadeOut 1s ease-in-out;
}

@keyframes fadeOut {
   from {
     opacity: 1;
   }
   to {
     opacity: 0;
   }
 }

@keyframes fadeIn {
   from {
     opacity: 0;
   }
   to {
     opacity: 1;
   }
 }

@media (max-width: 1024px) {
   .header{
      height: 80px;
   }
}