:root {
	--index: calc(1vw + 1vh);
}

.main__item1{
   display: flex;
   flex-direction: row;

   max-width: 1300px;
   height: 350px;
}

.item__img, .item__text{
   width: 50%;
   height: 100%;

   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}

.item__img .img a{
   width: 100%;
   height: 100%;
   display: block;

   position: absolute;
}

.item__img .img{
   width: 90%;
   height: 90%;

   position: relative;

   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;

   box-shadow: 0px 0px 20px rgba(250, 250, 250, 0.8);
   border-radius: 15px;

   transition: transform 0.5s;
}

.item__img .img:hover{
   transform: scale(1.01);
}


.item__text .title{
   width: 100%;
   height: 20%;

   font-size: calc(var(--index)*1.2);
   font-weight: 700;

   display: flex;
   align-items: center;

   padding-left: 10%;
}

.item__text .features{
   width: 100%;
   height: 10%;

   display: flex;
   align-items: center;
   padding-left: 10%;

   font-size: calc(var(--index)*0.7);
   color: grey;
}

.item__text .description{
   width: 100%;
   height: 70%;

   font-size: calc(var(--index)*0.8);
   font-weight: 300;
   line-height: 24px;
   letter-spacing: 1.2px;

   padding-left: 10%;

   display: flex;
   align-items: center;
}