.wrapper{
   max-width: 100%;
   height: 100%;

   overflow: hidden;
}

.header{
   width: 100%;
   height: 106px;

   position: fixed;
   top: 0;
   left: 0;

   z-index: 50;

   background-color: transparent;
}

.main{
   width: 100%;
   height: 100%;
}

.blur{
   width: 100%;
   height: 100%;

   position: absolute;
   top: 0;
   left: 0;
   z-index: 51;

   display: block;
   backdrop-filter: blur(5px);
}

.fadeOut{
   animation: fadeOut 1s ease-out;
}

@keyframes fadeOut {
   from {
     opacity: 1;
   }
   to {
     opacity: 0;
   }
 }
