:root {
	--index: calc(1vw + 1vh);
}

.bio{
   width: 100%;
   height: 100%;

   background-color: black;
}

.bio_container{
   max-width: 100%;
   width: 1300px;
   height: 100%;

   margin: 0 auto;
   padding-top: 106px;

   display: flex;
   justify-content: center;
   align-items: center;
}

.button{
   width: 300px;
   height: 100px;

   box-shadow: 0 0 15px rgb(250,250,250, 0.7);
   border-radius: 13px;
}

button{
   border-radius: inherit;
   width: inherit;
   height: inherit;

   color: whitesmoke;

   transition: font-size 0.8s;
}

button:hover{
   font-size: calc(var(--index)*0.8);;
}