.scene {
	height: 550vh;
}

:root {
	--index: calc(1vw + 1vh);
}

.layer {
	height: 100vh;
	width: 100vw;
	background-position: center;
	background-size: cover;
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: center;
	animation-timeline: scroll();
	animation-timing-function: linear;
	animation-fill-mode: both;
	animation-range: 0 300vh;
}

.layer:nth-child(1) { animation-name: animation-1; }
@keyframes animation-1 { to { scale: 1.25; } }

.layer:nth-child(2) { animation-name: animation-2; }
@keyframes animation-2 { to { scale: 1.275; } }

.layer:nth-child(3) { animation-name: animation-3; }
@keyframes animation-3 { to { scale: 1.5; } }

.layer:nth-child(4) { animation-name: animation-4; }
@keyframes animation-4 { to { scale: 2; } }

.layer:nth-child(5) {
	opacity: 0;
	animation-range: 250vh 300vh;
	animation-name: animation-end;
}
@keyframes animation-end { to { opacity: 1; } }

.layer__content {
   display: block;
	padding: 4vh 4.75vw;
	backdrop-filter: blur(5px);
   border-radius: 20px;
	animation-timeline: scroll();
	animation-timing-function: linear;
	animation-fill-mode: both;
	animation-range: 0 45vh;
	animation-name: animation-head;
}
@keyframes animation-head { to { opacity: 0; filter: blur(20px); } }

.layer__content h1 {
	text-transform: uppercase;
	font-size: calc(var(--index) * 3);
	text-align: center;
	line-height: .9em;
	letter-spacing: -.15vw;
   color: whitesmoke;
   font-weight: 900;
}

.layer__end{
   width: 1300px;
   height: 30vh;

   display: flex;
   flex-direction: row;
   align-items: center;

   margin: 0 auto;
}

.layer__end .features{
   width: 50%;
   height: 100%;

   display: flex;
   justify-content: center;
   align-items: center;

   position: relative;
}

.features span{
   color: whitesmoke;
}

.layer__end h3 {
   display: block;
   
   width: 48%;
   height: 100px;
   padding: 0 2.5%;


	font-size: calc(var(--index)*0.8);
   line-height: calc(var(--index));

	position: relative;

   color: whitesmoke;
}

.layer__end h3::after{
   content: '';

   position: absolute;
   right: 0;
   top: 15%;

   width: 2px;
   height: 80%;

   background-color: whitesmoke;
   border-radius: 2px;
}

.layer__end::before {
	content: '';
	background-color: black;
	inset: 0;
	position: absolute;
	opacity: 0;
	animation-timeline: scroll();
	animation-timing-function: linear;
	animation-fill-mode: both;
	animation-range: 300vh 450vh;
	animation-name: animation-bg;
}
@keyframes animation-bg { to { opacity: 1; } }
