.header__container{
   max-width: 1300px/* 84.64% */;
   height: 100%;

   display: flex;
   flex-direction: row;
   justify-content: space-between;

   margin: 0 auto;

   z-index: 10;

   color: whitesmoke;
}

.logo{
   width: 150px;
   height: 100%;

   display: flex;
   align-items: center;
   justify-content: center;

   font-size: 1.625em;
   font-weight: 900;
   letter-spacing: 3px;
}

.logo span{
   display: inline-block;
}

.nav{
   width: 46.15%;
   height: 100%;

   display: flex;
   flex-direction: row;
   justify-content: space-between;

}

.nav__home, .nav__projects, .nav__bio{
   width: 150px;
   height: 100%;
   position: relative;
}

.after{
   border-bottom: 2px solid whitesmoke;
}

.home__link, .projects__link, .bio__link{
   display: block;

   width: 100%;
   height: 100%;

}

.home__link button, .projects__link button, .bio__link button{
   width: 100%;
   height: 100%;

   font-size: 1em;
   color: whitesmoke;
}


.cursorArea{
   width: 40px;
   height: 40px;
   
   position: absolute;

   background-color: whitesmoke;
   filter: blur(20px);

   pointer-events: none;
   
   opacity: 0;
   transition: opacity 0.5s;
}

@media (max-width:1024px){
   .home__link button, .projects__link button, .bio__link button{
      font-size: 0.678em;
   }

   .logo{
      font-size: 1.3em;
   }

}